import {styled} from '@true/core';
import React, {memo, RefObject, useRef} from 'react';
import {IPolicy} from '@policy/store/policies/types';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {EReview} from '@policy/screens/PolicyDetailScreen/index';
import {AnimatedBottomSpace} from '@policy/components/AnimatedBottomSpace';
import {AnimatedKeyboardView} from '@policy/components/AnimatedKeyboardView';
import {CommentPreview} from '@true/ui';
import {PreviewCommentList} from '@true/ui';

interface Props {
  policy?: IDetailPolicy;
  isApprovable?: EReview;
  listRef?: RefObject<PreviewCommentList>;
}

export const DetailDiscussions = memo((props: Props) => {
  const {aBottom} = useAnimatedSafeAreaInsets();

  const animatedStyle = useAnimatedStyle(() => ({
    marginBottom: -aBottom.value,
  }));

  return (
    <SContainer style={props.isApprovable !== -1 ? animatedStyle : undefined}>
      <CommentPreview.List ref={props.listRef} />
      {/*<CommentPreview.Input />*/}
      {/*<AnimatedBottomSpace />*/}
    </SContainer>
  );
});

const SContainer = styled(Animated.View)`
  padding-bottom: 16px;
`;
