import React, {memo} from 'react';
import FastImage from 'react-native-fast-image';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {StyleSheet} from 'react-native';
import {useNavigation} from '@react-navigation/native';
import {IC_SEARCH, styled, useMe} from '@true/core';
import {IMAGE_MENU_HEADER_BOOKING} from '@true/asset';

import {Avatar, UIButton, UIText} from '@true/ui';

export const MenuHeader = memo(() => {
  const {aTop} = useAnimatedSafeAreaInsets();
  const me = useMe();

  const animatedStyle = useAnimatedStyle(() => {
    return {
      marginTop: aTop.value,
    };
  });

  return (
    <SWrapper>
      <SContainer
        style={{
          ...StyleSheet.absoluteFillObject,
          zIndex: 10,
        }}>
        <SHeaderContainer style={animatedStyle}>
          <SAvatar userKey={me?.id || 0} size={48} />
          <SViewInfo>
            <STextName numberOfLines={1}>{me?.name}</STextName>
            {me?.title ? (
              <STextDesc numberOfLines={1}>{me?.title}</STextDesc>
            ) : null}
          </SViewInfo>
        </SHeaderContainer>
      </SContainer>
      <SBgHeader source={IMAGE_MENU_HEADER_BOOKING} />
    </SWrapper>
  );
});

const SWrapper = styled.View`
  background-color: ${p => p.theme.neutral9};
`;

const SBgHeader = styled(FastImage)`
  height: 119px;
  width: 100%;
`;

const SAvatar = styled(Avatar)``;

const SViewInfo = styled.View`
  margin-left: 12px;
  align-items: flex-start;
  justify-content: center;
  flex: 1;
`;

const STextName = styled(UIText.HeaderMedium)`
  color: ${p => p.theme.white};
  margin-bottom: 4px;
`;

const STextDesc = styled(UIText.CaptionMedium400)`
  color: ${p => p.theme.white};
`;

const SContainer = styled.View`
  z-index: 10;
  border-radius: 12px;
  padding: 0 16px;
  margin-bottom: 12px;
`;

const SHeaderContainer = styled(Animated.View)`
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const SButtonAvatar = styled.TouchableOpacity``;

const SButton = styled(UIButton).attrs(p => ({
  imageSize: 24,
  imageStyle: {
    tintColor: '#fff',
  },
}))``;

const STitle = styled(UIText.HeaderLarge)`
  color: ${p => p.theme.white};
  margin-left: 8px;
`;

const SSearchButton = styled(UIButton)`
  height: 36px;
  flex-direction: row;
  background-color: ${p => p.theme.white};
  border-radius: 8px;
  padding: 8px 12px;
  justify-content: space-between;
  align-items: center;
`;

const SSearchTitle = styled(UIText.BodyMedium400)`
  color: ${p => p.theme.neutral2};
  flex: 1;
`;

const SSearchIcon = styled(FastImage).attrs(p => ({
  source: IC_SEARCH,
  tintColor: p.theme.neutral3,
}))`
  width: 20px;
  height: 20px;
`;
